import React, { useEffect, useRef, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { LoginForm, LoginWithPassword } from "./auth";
import {
	Container,
	Row,
	Col,
	ProgressBar,
	Nav,
	Form,
	Button,
	Modal,
	Navbar,
} from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { withRouter } from "react-router";

import { Carousel } from "react-bootstrap";
import intro from "../data/intro.json";
// import { navBarMenuItems } from "../config";

const AssessmentCheckModal = (props) => {
	const dispatch = useDispatch();
	const history = useHistory();
	const onClick = (path) => {
		history.push(path);
		dispatch({ type: "closeAssessmentCheckModal" });
	};
	return (
		<Modal
			show={props.show}
			onHide={props.onClose}
			centered
			backdrop="static"
		>
			<Modal.Header closeButton />
			<Modal.Body className="text-center">
				<p> Have you taken the CQ Assessment before?</p>
				<p>
					<Button
						className="btn btn-primary btn-outline"
						onClick={() => onClick("/login")}
					>
						Yes
					</Button>
					<br />
					<br />
					<Button
						className="btn btn-primary "
						onClick={() => onClick("/assessment")}
					>
						No
					</Button>
				</p>
			</Modal.Body>
		</Modal>
	);
};

const AssessmentHalfWay = ({ data, onContinue }) => {
	return (
		<Row className="flex-center text-center">
			<Col className="p-0 text-center">
				<img
					src={data.imageSrc}
					width={320}
					alt="Assessment Complete"
				/>
				<div className="my-3 text-center">
					<p className="h4 mb-3 font-weight-bold w-50 m-auto text-center">
						{data.title}
					</p>
					<p className="mb-3">{data.text}</p>
					<ProgressBar now={data.percentComplete} />
					<br />
					<p className="font-weight-bold">
						{data.percentComplete}% complete
					</p>
					<Button variant="primary" onClick={onContinue}>
						Continue
					</Button>
				</div>
			</Col>
		</Row>
	);
};

const AssessmentCompleteHeader = (props) => (
	<div>
		<img
			src="../assets/breaks/goal-animate.svg"
			alt="Assessment Complete"
		/>
		{/* <img className="my-5" src="/assets/svgs/assessment_complete.png" alt="Assessment Complete" /> */}
		<div className="my-5">
			<p className="h4 font-weight-bold">Congratulations!</p>
			<p>Youve completed the CQ Assessment</p>
		</div>
	</div>
);

const AssessmentCompleteFooter = (props) => {
	const viewReportButtonRef = useRef(null);
	useEffect(()=>{
			viewReportButtonRef.current?.scrollIntoView();
	}, [props.isSubmissionComplete]);
	return props.isSubmissionComplete ? (
		<div>
			<p className="font-weight-bold">Your Results Are Ready To View</p>
			<p>
				Please take a look at your analytics and your future career
				oppurtunities!
			</p>
			<Link className="btn btn-primary" to="/report" ref={viewReportButtonRef}>
				View Report
			</Link>
			<br/>
			&nbsp;
		</div>
	) : (
		<div>
			<p className="font-weight-bold">Result Is Being Calculated</p>
			<p className="text-muted">
				Please give us some time to fetch the results for you
			</p>
			<br />
			<div className="m-auto">
				<ProgressBar animated={true} now={90} min={0} max={100} />
			</div>
		</div>
	);
};

const AssessmentComplete = (props) => {
	if (props.report === "error") {
		return (
			<Container className="expanded" fluid>
				<Row className="justify-content-center">
					<Col className="text-center" md={4}>
						<img
							className="my-5"
							src="/assets/svgs/assessment_complete.png"
							alt="Assessment Complete"
						/>
						<p className="font-weight-bold">
							Your Results Are Ready To View
						</p>
						<p>
							Please take a look at your analytics and your future
							career oppurtunities!
						</p>
						<Link>Sign Up</Link>
					</Col>
				</Row>
			</Container>
		);
	} else {
		return (
			<div className="fullscreen flex-center">
				<Row className="justify-content-center">
					<Col className="text-center">
						<AssessmentCompleteHeader />
						<AssessmentCompleteFooter
							isSubmissionComplete={props.isSubmissionComplete}
						/>
					</Col>
				</Row>
			</div>
		);
	}
};

const AssessmentIntroduction = ({ onStart }) => {
	const [slideIndex, setSlideIndex] = useState(0);
	return (
		<Carousel interval={null} indicators={false} activeIndex={slideIndex}>
			{intro.map((slide, index) => (
				<Carousel.Item key={index}>
					<AssessmentIntroductionSlide {...slide} />
					<footer className="mt-1 text-center">
						{index === 2 ? (
							<Button variant="primary" onClick={onStart}>
								Start
							</Button>
						) : (
							<Button
								variant="primary"
								onClick={() => setSlideIndex(index + 1)}
							>
								Next
							</Button>
						)}
					</footer>
				</Carousel.Item>
			))}
		</Carousel>
	);
};

const AssessmentIntroductionSlide = ({
	index,
	title,
	text,
	imageSrc,
	infoText,
	onStart,
	onNext,
}) => {
	return (
		<article className="text-center">
			<header></header>
			<section className="row justify-content-center align-items-center">
				<div className="col-md-5">
					<h1 className="font-weight-bold mt-20">CQ Assessment</h1>
					<img width="250" className="cq_img" height="250" src={imageSrc} alt="" />
					<h3 className="font-weight-bold">{title}</h3>
					<p className="text-landing">{text}</p>
					{/* <p className="text-gold h6 font-weight-bold my-4">Ready to get started?</p> */}
					<div>
						{/* <a className="text-muted" href="">{infoText}</a> */}
					</div>
				</div>
			</section>
		</article>
	);
};

const CQappbar = (props) => {
	const dispatch = useDispatch();
	const logout = () => {
		props.history.push("/login");
		props.logOut();
	};
	return (
		<Navbar collapseOnSelect expand="lg">
			<Navbar.Brand href="/">
				<img
					className="brand"
					src="assets/CareerQuotient.png"
					alt="Career Quotient Logo"
				/>
			</Navbar.Brand>
			<Navbar.Toggle aria-controls="responsive-navbar-nav" />
			<Navbar.Collapse id="responsive-navbar-nav">
				<Nav className="ml-auto">
					<Nav.Link>
						<Link to="/">Home</Link>
					</Nav.Link>
					<Nav.Link>
						<Link
							onClick={() =>
								dispatch({ type: "openAssessmentCheckModal" })
							}
						>
							CQ Assessment
						</Link>
					</Nav.Link>
					{props.user ? (
						<Nav.Link>
							<Link onClick={logout} to={"/"}>
								Logout
							</Link>
						</Nav.Link>
					) : (
						<Nav.Link>
							<Link to={"/login"}>Login</Link>
						</Nav.Link>
					)}
				</Nav>
			</Navbar.Collapse>
		</Navbar>
	);
};

function mapStateToProps(state) {
	return { user: state.auth.user };
}

const mapDispatchToProps = (dispatch) => {
	return {
		logOut: () => dispatch({ type: "auth/removeUser" }),
	};
};

const _Appbar = withRouter(CQappbar);

const Appbar = connect(mapStateToProps, mapDispatchToProps)(_Appbar);

const Loading = (props) => {
	return <div>Loading...</div>;
};

const ContactForm = (props) => {
	return (
		<Form>
			<Form.Group controlId="formBasicEmail">
				<Form.Control type="text" placeholder="Full Name" />
			</Form.Group>
			<Form.Group controlId="formBasicEmail">
				<Form.Control type="email" placeholder="Email ID" />
			</Form.Group>
			<Form.Group controlId="formBasicEmail">
				<Form.Control
					type="email"
					placeholder="Tell us how we can help you"
				/>
			</Form.Group>
			<Form.Label>You Want To:</Form.Label>
			<Form.Group className="mt-0 mb-5" controlId="formBasicEmail">
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Accelerate Your Career Progress"
				/>
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Run an assessment for your employees"
				/>
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Partner with us for your students training"
				/>
			</Form.Group>
			<Button variant="primary" className="assesBtn" block>
				Send 
			</Button>
		</Form>
	);
};

export { AssessmentQuestion } from "./assessment.js";

export {
	Appbar,
	LoginForm,
	LoginWithPassword,
	ContactForm,
	AssessmentIntroduction,
	AssessmentComplete,
	AssessmentHalfWay,
	AssessmentIntroductionSlide,
	Loading,
	AssessmentCheckModal,
};
