import { connect } from "react-redux";
import React from "react";
import {
	Loading,
	AssessmentComplete,
	AssessmentIntroduction,
	AssessmentHalfWay,
	AssessmentQuestion,
	LoginForm,
} from "../components";
import CQAPI from "../api";
import intermissionData from "../data/intermissions.json";
import AssessmentReport from "../routes/report";
import { Alert } from "react-bootstrap";

import { CSSTransition } from "react-transition-group";
import { SignUpForm } from "../components/auth";
import { Redirect } from "react-router-dom";

const AssessmentStatus = {
	loading: "loading",
	new: "new",
	started: "started",
	intermission: "intermission",
	completed: "completed",
	error: "error",
};

const questionChangeDelay = 600;
const transistionDelay = 400;

class Assessment extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			index: 0,
			isRedoAvailable: false,
			isSubmitting: false,
			answers: [],
			questions: [],
			assessmentStatus: AssessmentStatus.loading,
			halfwayComplete: false,
			shouldTransation: false,
			slideIndex: 0,
			isSubmitSuccess: false,
			isSubmitFalse: false,
			shouldAnimate: false,
		};
		this.handler = this.handler.bind(this);
		this.setAnswer = this.setAnswer.bind(this);
		this.assessmentStartedHandler = this.assessmentStartedHandler.bind(
			this
		);
	}

	componentDidMount() {
		CQAPI.getQuestions()
			.then((resp) => {
				if (resp) {
					// TODO(rohan): API Validation
					//
					// On success, add the intermission items
					// to the orignal question set and setState
					var questionListWithIntermissions = resp;

					this.setState({
						questions: questionListWithIntermissions,
						assessmentStatus: AssessmentStatus.new,
						shouldTransation: true,
					});
				}
			})
			.catch((err) =>
				this.setState({ assessmentStatus: AssessmentStatus.error })
			);
	}

	handler(option) {
		if (option === 0 && this.state.isRedoAvailable) {
			if (this.state.index !== 0) {
				this.setState({
					index: this.state.index - 1,
					isRedoAvailable: false,
					shouldTransation: false,
					answers: this.state.answers.slice(
						0,
						this.state.answers.length - 1
					),
					shouldAnimate: true,
				});
			}
		} else {
			if (this.state.index < this.state.questions.length) {
				setTimeout(() => {
					if (
						this.state.assessmentStatus !==
						AssessmentStatus.intermission
					) {
						this.setState({
							index: this.state.index + 1,
							isRedoAvailable: true,
							shouldTransation: false,
							shouldAnimate: true,
							assessmentStatus:
								[12, 23, 33, 44].indexOf(this.state.index) >= 0
									? AssessmentStatus.intermission
									: AssessmentStatus.started,
						});
					}
				}, questionChangeDelay);
			}
		}
		setTimeout(() => {
			this.setState({ shouldAnimate: false });
		}, transistionDelay);
	}

	setAnswer(option) {
		this.setState(
			{
				answers: this.state.answers.concat([option]),
			},
			() => {
				// Check if We have reached the end of the question list
				if (this.state.index + 1 === this.state.questions.length) {
					this.props.onComplete(this.state.answers);
					this.setState({
						assessmentStatus: AssessmentStatus.completed,
					});
				} else {
					// Increment Question Count
					if (this.state.index <= this.state.questions.length) {
						this.handler(1);
					}
				}
			}
		);
	}

	assessmentStartedHandler() {
		this.setState({
			assessmentStatus: AssessmentStatus.started,
			shouldAnimate: true,
		});

		if (this.state.index === 48) {
			setTimeout(() => {
				this.setState({ shouldAnimate: false });
			}, transistionDelay);
		}
	}

	RenderChild = () => {
		switch (this.state.assessmentStatus) {
			case AssessmentStatus.error:
				return <Alert variant="danger">Oops! An Error Occurred</Alert>;
			case AssessmentStatus.new:
				return (
					<AssessmentIntroduction
						onStart={this.assessmentStartedHandler}
					/>
				);
			case AssessmentStatus.intermission:
				return (
					<AssessmentHalfWay
						data={intermissionData[`${this.state.index}`]}
						onContinue={this.assessmentStartedHandler}
					/>
				);
			case AssessmentStatus.completed:
				return <Redirect to="/sign-up" />;
			case AssessmentStatus.started: {
				if (this.state.questions[this.state.index]) {
					return (
						<AssessmentQuestion
							allowMultipleSelection={
								this.state.questions[this.state.index]
									.allowMultipleSelection
							}
							isRedoAvailable={this.state.isRedoAvailable}
							index={this.state.index}
							question={this.state.questions[this.state.index]}
							option={
								this.state.questions[this.state.index].options
							}
							setAnswer={this.setAnswer}
							onRedo={() => this.handler(0)}
							disabled={this.state.shouldAnimate}
						/>
					);
				} else {
					return <Loading />;
				}
			}

			default:
				return <Loading />;
		}
	};

	render() {
		return (
			<div className="fullscreen flex-center">
				<CSSTransition
					in={this.state.shouldAnimate}
					timeout={800}
					classNames="question"
				>
					<this.RenderChild />
				</CSSTransition>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return { auth: state.auth };
}

const mapDispatchToProps = (dispatch) => {
	return {
		signUp: (user) => dispatch({ type: "auth/signUp", payload: user }),
		onComplete: (answers) =>
			dispatch({ type: "assessment/completed", payload: answers }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Assessment);
